import { Component, OnDestroy } from '@angular/core';
import { Subscription } from 'rxjs';
import { AlertType } from './alertType.enum';
import { FfsjAlertService } from './ffsj-alert.service';

@Component({
  selector: 'lib-ffsj-alert',
  standalone: true,
  imports: [],
  templateUrl: './ffsj-alert.component.html',
  styleUrl: './ffsj-alert.component.scss'
})
export class FfsjAlertComponent implements OnDestroy {
  public message: string | null = null;
  public type!: AlertType;
  public subscription: Subscription;

  constructor(private alertService: FfsjAlertService) {
    console.log('FfsjAlertComponent construido');
    this.subscription = this.alertService.alert$.subscribe(alert => {
      this.message = alert.message;
      this.type = alert.type;
      setTimeout(() => this.closeAlert(), alert.duration);
    });
  }

  ngOnDestroy(): void {
    this.subscription.unsubscribe();
  }

  closeAlert(): void {
    this.message = null;
  }
}
