import { Component, Inject } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogModule, MatDialogRef } from "@angular/material/dialog";
import { AlertButtonType } from './alertButtonType.enum';

export interface DataAlert {
  title: string;
  content: string;
  buttonsAlert: AlertButtonType[];
}

@Component({
  selector: 'lib-ffsj-dialog-alert',
  standalone: true,
  imports: [ MatDialogModule ],
  templateUrl: './ffsj-dialog-alert.component.html',
  styleUrl: './ffsj-dialog-alert.component.scss'
})
export class FfsjDialogAlertComponent {

  constructor(
    public dialogSelfRef: MatDialogRef<FfsjDialogAlertComponent>,
    @Inject(MAT_DIALOG_DATA) public data: DataAlert
  ) { }

}
